import React, {useState, useEffect, useContext} from 'react';
import {Link} from 'react-router-dom';
import './Header.css';
import catalog from '../../assets/catalog-icon.svg';
import cart from '../../assets/cart-icon.svg';
import account from '../../assets/account-icon.svg';
import { CartContext } from "../../providers/CartProvider";

export const PHONE_WIDTH = 576;

export function resizeHandle(width)  {
    window.addEventListener('resize', width);

    return () => {
        window.removeEventListener('resize', width);
    };
} 

function Header({ children }) {
    // Storage a width of user display for adaptive
    const [width, setWidth] = useState(window.innerWidth);
    // Storage a cart status
    const [notify, setNotify] = useContext(CartContext);

    useEffect(() => {
        const handleResize = (event) => {
            setWidth(event.target.innerWidth);
        };

        resizeHandle(handleResize);
    }, []);

    return width >= PHONE_WIDTH ? (
        <>
            <header className="header">
                <nav className="header__nav">
                    <ul>
                        <Link to="/vinyl-store">
                            <li className="header__list">
                                <a className="header__link" href="/vinyl-store"> </a>
                            </li>
                        </Link>
                        <Link to="/catalog">
                            <li className="header__list">
                                <a className="header__link" href="/catalog"></a>
                            </li>
                        </Link>
                        <Link to="/cart">
                            <li className="header__list">
                                <a className="header__link" href="/cart"></a>
                                {notify.length > 0 && (
                                    <div className="notify">
                                        <p className="notify__text">1</p>
                                    </div>
                                )}
                            </li>
                        </Link>
                        <Link to="/account">
                            <li className="header__list">
                                <a className="header__link" href="/account"></a>
                            </li>
                        </Link>
                    </ul>
                </nav>
            </header>
            <div className="heading">
                <h1 className="h1">{children}</h1>
                <p className="info__text">     .</p>
            </div>
        </>

    ) : (
        <>
            <header className="header">
                <nav className="header__nav">
                    <ul>
                        <Link to="/catalog">
                            <li className="header__list">
                                <a className="header__link" href="/catalog">
                                    <div className="icon">
                                        <img className="icon__image" src={catalog} alt="Catalog" />
                                    </div>
                                </a>
                            </li>
                        </Link>
                        <Link to="/cart">
                            <li className="header__list">
                                <a className="header__link" href="/cart">
                                    <div className="icon">
                                        <img className="icon__image" src={cart} alt="Cart" />
                                    </div>
                                </a>
                                {notify.length > 0 && (
                                    <div className="notify">
                                        <p className="notify__text">1</p>
                                    </div>
                                )}
                            </li>
                        </Link>
                        <Link to="/account">
                            <li className="header__list">
                                <a className="header__link" href="/account">
                                    <div className="icon">
                                        <img className="icon__image" src={account} alt="Account" />
                                    </div>
                                </a>
                            </li>
                        </Link>
                    </ul>
                </nav>
            </header>
            <div className="heading">
                <h1 className="h1">{children}</h1>
                <p className="info__text">     .</p>
            </div>
        </>
    );
}

export default Header;